import 'formCommonApi';

/**
 * 
 * @class GM.Form.FormData
 */
GM.Form.FormData = {
    /**
     * 
     */
    init: function(formid) {
        this._initScopeVar();
        this._bindEvents(formid);

        GM.Form.formScope.gridQueryParams = {
            queryType: undefined,
            queryValue: undefined
        };

        //
        if (GM.Form.parameters.formDataSourceNames && GM.Form.parameters.formDataSourceNames.length > 0) {
            this._initOptions(GM.Form.formScope, GM.Form.parameters.formDataSourceNames);
        }
    },
    _datas: {
        globe: {}, //
        saveKeys: {} //
    },
    _initScopeVar: function() {
        GM.Form.formScope.result = {}; //
    },
    _bindEvents: function(formid) {
        var _this = this;

        //
        GM.Form.formScope.getData = function() {
            if (!GM.Form.parameters.urlParams.did) {
                _this._getData(formid);
            } else if (GM.Form.parameters.urlParams.did) {
                _this._getDraftData(GM.Form.parameters.urlParams.did);
            }
        };

        //
        GM.Form.formScope.save = function(successCallback, failureCallback) {
            _this._saveData(false, successCallback, failureCallback);
        };

        //
        GM.Form.formScope.saveAndHandOver = function() {
            _this._saveData(true);
        };

        GM.Form.formScope.saveDraft = function() {
            _this._saveDraft(GM.Form.parameters.urlParams.duuid || GM.Form.parameters.urlParams.did);
        };

        GM.Form.formScope.getSingleDataGrid = function(formid, ds) {
            _this._getSingleDataGrid(GM.Form.formScope, formid, ds);
        };

        GM.Form.formScope.dataGridSelect = function(item) {
            _this._dataGridSelect(GM.Form.formScope, item);
            item.grid.appScope.closeThisDialog();
        };

        GM.Form.formScope.saveDialog = function() {
            _this._saveDialog();
            GM.Form.formScope.closeFormDialog();
        };

        GM.Form.formScope.dgA = function(item) {
            _this._dataGridOperA(GM.Form.formScope, item);
        };

        GM.Form.formScope.dgB = function(item) {
            _this._dataGridOperB(GM.Form.formScope, item);
        };

        GM.Form.formScope.getRedisString = function(key) {
            return _this._getRedisString(key);
        };

        GM.Form.formScope.removeRedisString = function(key) {
            _this._removeRedisString(key);
        };

        GM.Form.formScope.redisString = function(key, data) {
            _this._redisString(key, data);
        };

        GM.Form.formScope.initDataGridOptions = function(formid) {
            _this._initDataGridOptions(GM.Form.formScope, formid);
        };

        GM.Form.formScope.initDataGridHeaders = function(ds, fieldString, operString) {
            _this._getGridOption(GM.Form.formScope, ds, null, null, fieldString, operString);
        };
    },
    /**
     * 
     *
     * @param {Object} scope    
     * @param {Array} dgds      
     */
    _initOptions: function(scope, dgds) {
        if (!dgds || dgds.length === 0) {
            return;
        }

        for (var i = 0; i < dgds.length; i++) {
            if (dgds[i]) {
                var grid = $("div[ui-grid='" + dgds[i] + "Option']");
                var sDataItems, sTemplateItems;

                if (grid.length > 0) {
                    sDataItems = grid.attr('data-items');
                    sTemplateItems = grid.attr('template-items');
                }

                this._getGridOption(scope, dgds[i], null, null, sDataItems, sTemplateItems);
            }
        }
    },
    /**
     * formid
     *
     * @param {Object} scope 
     * @param {String} formid ID
     */
    _initDataGridOptions: function(scope, formid) {
        var _this = this;
        var url = window.FORM_REST_URL.workflow.getDataGridDataSourceNames + '?formids=' + formid;
        GM.Form.CommonOper.syncAjaxGet(url, function(res) {
            if (res.success === true && res.data) {
                _this._initOptions(scope, res.data);
            }
        }, function(res) {});
    },
    /**
     * Redis
     *
     * @param {String} key 
     */
    _getRedisString: function(key) {
        var result;
        GM.Form.CommonOper.syncAjaxGet(window.FORM_REST_URL.workflow.getRedisString + '?key=' + key, function(res) {
            if (res.success === true && !!res.data) {
                result = res.data;
            }
        }, function(res) {});

        return result;
    },
    /**
     * Redis
     *
     * @param {String} key 
     */
    _removeRedisString: function(key) {
        GM.Form.CommonOper.syncAjaxGet(window.FORM_REST_URL.workflow.removeRedisString + '?key=' + key, function(res) {}, function(res) {});
    },
    /**
     * Redis
     *
     * @param {String} key 
     * @param {String} data 
     */
    _redisString: function(key, data) {
        GM.Form.CommonOper.syncAjaxPost(window.FORM_REST_URL.workflow.getRedisString + '?key=' + key, function(res) {}, function(res) {}, {
            key: key,
            data: data
        });
    },
    /**
     * 
     *
     * @param {Object} scope    
     * @param {Object} item     
     */
    _dataGridOperA: function(scope, item) {
        var ele = event.srcElement;

        if (!ele) {
            return;
        }

        var linkText = $(ele).text();
        var templates = item.grid.element.attr('template-items').split(',');
        var formid;

        for (var i = 0; i < templates.length; i++) {
            var arr = templates[i].split(':');

            if (arr.length < 3) {
                continue;
            }

            if (arr[1] == linkText) {
                formid = arr[2];
                break;
            }
        }

        if (formid) {
            this._showDataGridDlg(scope, item, formid);
        }
    },
    /**
     * REST
     *
     * @param {Object} scope    
     * @param {Object} item     
     */
    _dataGridOperB: function(scope, item) {
        var ele = event.srcElement;

        if (!ele) {
            return;
        }

        var linkText = $(ele).text();
        var templates = item.grid.element.attr('template-items').split(',');
        var service, method, tip, msg;

        for (var i = 0; i < templates.length; i++) {
            var arr = templates[i].split(':');

            if (arr.length < 3) {
                continue;
            }

            if (arr[1] == linkText) {
                service = arr[2];

                if (arr[3]) {
                    method = arr[3];
                }

                if (arr[4]) {
                    tip = arr[4].replace(/RDS_COLON/g, ':');
                }

                if (arr[5]) {
                    msg = arr[5];
                }

                break;
            }
        }

        if (service) {
            var _this = this;

            if (tip) {
                GM.Alert.show('', tip, {
                    showCancelButton: true
                }, function() {
                    _this._invokeDataGridREST(scope, item, service, method, msg);
                });
            } else {
                _this._invokeDataGridREST(scope, item, service, method, msg);
            }
        }
    },
    /**
     * 
     *
     * @param {Object} scope    
     * @param {Object} item     
     * @param {String} formid   formid
     */
    _showDataGridDlg: function(scope, item, formid) {
        var ds = item.grid.element.attr('data-items');

        //
        if (ds) {
            var arr = ds.split(',');

            for (var u = 0; u < arr.length; u++) {
                if (arr[u]) {
                    var arrDataItems = arr[u].split(':');

                    if (arrDataItems.length === 3) {
                        GM.Form.parameters.urlParams[arrDataItems[2]] = item.entity[arrDataItems[0]];
                        break;
                    }
                }
            }
        }

        GM.Form.formScope.showFormDialog(null, formid);
        _this._getData(formid, true);
    },
    /**
     * REST
     *
     * @param {Object} scope    
     * @param {Object} item     
     * @param {String} service  service
     * @param {String} method   GET  POST
     * @param {String} msg      
     */
    _invokeDataGridREST: function(scope, item, service, method, msg) {
        if (!RESTURL.dg || !RESTURL.dg[service]) {
            GM.Form.CommonOper.showFailureMsg('[' + service + ']');
            return;
        }

        var _this = this;

        var formid = item.grid.element.attr('formid');
        var dataItems = item.grid.element.attr('data-items');
        var ds = item.grid.element.attr('ui-grid');

        if (ds) {
            ds = ds.replace('Option', '');
        }

        var param = {};

        //
        if (dataItems) {
            var arr = dataItems.split(',');

            for (var u = 0; u < arr.length; u++) {
                if (arr[u]) {
                    var arrDataItems = arr[u].split(':');

                    if (arrDataItems.length === 3) {
                        param[arrDataItems[2]] = item.entity[arrDataItems[0]];
                        break;
                    }
                }
            }
        }

        var invoke;

        if (method && method.toUpperCase() == 'POST') {
            invoke = GM.Form.restful.post(RESTURL.dg[service], param);
        } else {
            invoke = GM.Form.restful.get(RESTURL.dg[service], param);
        }

        invoke.then(function(res) {
            if (res.success === true && !!formid && !!ds) {
                _this._getSingleDataGrid(scope, formid, ds);
            } else if (res.success === false) {
                GM.Form.CommonOper.showFailureMsg(res.message);
            }
        }, function(res) {
            GM.Form.CommonOper.showFailureMsg(res.message);
        });
    },
    /**
     * 
     *
     * @param {Object} scope
     * @param {Object} item
     */
    _dataGridSelect: function(scope, item) {
        var ds = item.grid.element.attr('ui-grid');

        if (ds) {
            ds = ds.replace('Option', '');
            var paras = scope[ds + 'KeyParas'];

            if (paras && paras.length > 0) {
                for (var u = 0; u < paras.length; u++) {
                    GM.Form.parameters.urlParams[paras[u].para] = item.entity[paras[u].key];
                }
            }

            if (scope.gridQueryParams && !!scope.gridQueryParams.queryType && !!scope.gridQueryParams.queryValue) {
                GM.Form.parameters.urlParams.searchType = scope.gridQueryParams.queryType;
                GM.Form.parameters.urlParams.searchValue = scope.gridQueryParams.queryValue;
            }
        }

        this._getData(false, true);
        scope.closeFormDialog();
    },
    /**
     * 
     *
     * @param {Object} scope    
     * @param {String} formid   formid
     * @param {String} ds       
     */
    _getSingleDataGrid: function(scope, formid, ds) {
        var _this = this;
        var para = angular.copy(GM.Form.parameters.urlParams);

        if (!para.ywh) {
            para.ywh = para.did || para.duuid;
        }

        if (scope.gridQueryParams.queryValue) {
            para.queryType = scope.gridQueryParams.queryType;
            para.queryValue = scope.gridQueryParams.queryValue;
            scope.gridQueryParams = {
                queryType: '',
                queryValue: ''
            };
        }

        var up = {
            'up': para
        };

        var param = {
            formids: formid,
            dataSourceName: ds,
            urlParameters: JSON.stringify(up)
        };
        GM.Form.restful.get(GM.Form.parameters.servers.getSingleDataGridServer, param).then(function(res) {
            if (res.success === true && res.data) {
                _this._bindDataGrid(scope, ds, res.data);
            }
        }, function(res) {});
    },
    /**
     * 
     *
     * @param {Object} scope            
     * @param {String} ds               
     * @param {Array} data              
     * @param {Array} hd                
     * @param {String} dataItems        hd
     * @param {String} templateItems    
     */
    _getGridOption: function(scope, ds, data, hd, dataItems, templateItems) {
        var gridOptionName = ds + 'Option';
        scope[gridOptionName] = {};

        var gridOptions = {};
        var tableGrid = new GM.Form.gmTable();
        var templates = [],
            heads = [];

        if (templateItems) {
            var arrTemps = templateItems.split(',');
            for (var i = 0; i < arrTemps.length; i++) {
                if (arrTemps[i] != '') {
                    var arr = arrTemps[i].split(':');
                    templates.push({
                        name: arr[1],
                        click: arr[0]
                    });
                }
            }
        }

        if (dataItems) {
            var arrTemps = dataItems.split(',');
            var gridParas = [];

            for (var i = 0; i < arrTemps.length; i++) {
                if (arrTemps[i] != '') {
                    var arr = arrTemps[i].split(':');
                    heads.push({
                        name: arr[0],
                        displayName: arr[1],
                        enableColumnMenu: false,
                        cellTooltip: true
                    });

                    if (arr.length === 3) {
                        gridParas.push({
                            key: arr[0],
                            para: arr[2]
                        });
                    }
                }
            }

            if (gridParas.length > 0) {
                scope[ds + 'KeyParas'] = gridParas;
            }
        } else if (hd && hd.length > 0) {
            for (var i = 0; i < hd.length; i++) {
                heads.push({
                    name: hd[i],
                    displayName: hd[i],
                    enableColumnMenu: false,
                    cellTooltip: true
                });
            }
        }

        if (templates.length > 0) {
            var strCellTemplate = tableGrid.getCellTemplate(templates);

            heads.push({
                name: 'operate',
                displayName: '',
                enableColumnMenu: false,
                cellTemplate: strCellTemplate
            });
        }

        gridOptions.data = data;
        gridOptions.columnDefs = heads;
        scope[gridOptionName] = gridOptions;
        tableGrid.updateSize();
    },
    _bindDataGrid: function(scope, ds, data) {
        var _this = this;

        for (var key in data) {
            if (!!ds && key != ds && key != 'page__' + ds) {
                continue;
            }

            if (key.indexOf('dg__') === 0) {
                if (!(data[key] instanceof Array)) {
                    var dgData = data[key];
                    var sDataItems = $("div[ui-grid='" + key + "Option']").attr('data-items');
                    var sTemplateItems = $("div[ui-grid='" + key + "Option']").attr('template-items');

                    _this._getGridOption(scope, key, dgData.data.page.records, dgData.headList, sDataItems, sTemplateItems);
                } else if ((scope.pstyle == 'landtree' || scope.isDataComplex === true) && scope.globalTreeData[key]) {
                    scope.globalTreeData[key] = data[key];
                    //updateDataGridsBind($scope.focusNode);
                }
            }

            if (key && key.indexOf('page__') === 0) {
                scope[key] = data[key];
            }
        }
    },
    _getDraftData: function(key) {
        GM.Form.restful.get(RESTURL.process.getDraftInfo + '?id=' + key).then(function(response) {
            if (response.success === true && response.data != null) {
                GM.Form.formScope.result = JSON.parse(response.data);
            }
        }, function(response) {});
    },
    _saveDraft: function(key) {
        var param = {
            id: key,
            userId: top.GM.userInfo.loginName,
            appId: top.GM.AppInfo.id,
            processDefId: GM.Form.parameters.urlParams.processDefId,
            processName: GM.Form.parameters.urlParams.processName,
            processKey: GM.Form.parameters.urlParams.processKey
        };

        var ctrl = angular.element("[data-role='bizname']");

        if (ctrl && ctrl > 0) {
            param.bizName = ctrl.val();
        }

        GM.Form.restful.get(RESTURL.process.saveDraftBaseInfo, param).then(function(res) {
            if (res.success === true) {
                param = {
                    id: key,
                    data: _this._getSavingData()
                };
                GM.Form.restful.post(RESTURL.process.saveDraftInfo, param).then(function(r) {
                    if (r.success === true) {
                        GM.Form.CommonOper.showSuccessMsg('');
                    } else {
                        GM.Form.CommonOper.showFailureMsg(r.message);
                    }
                }, function(r) {
                    GM.Form.CommonOper.showFailureMsg(r.message);
                });
            }
        }, function(res) {
            GM.Form.CommonOper.showFailureMsg(res.message);
        });
    },
    /**
     * 
     *
     * @param {String} formid
     * @param {Boolean} keepOldData 
     */
    _getData: function(formid, keepOldData) {
        var _this = this;

        var params = {
            formids: (formid || GM.Form.parameters.formIds.join(',')),
            urlParameters: JSON.stringify(GM.Form.parameters.urlParams)
        };

        GM.Form.restful.get(GM.Form.parameters.servers.getDataServer, params).then(function(res) {
            if (res.success === false) {
                GM.Form.CommonOper.showFailureMsg('' + res.message);
                return;
            }

            _this._datas.globe = res.data;
            console.log(_this._datas.globe);
            console.log(GM.Form.formScope.result);
            if (!_this._datas.globe.treeKey) {
                if (!keepOldData) {
                    GM.Form.formScope.result = _this._datas.globe;
                } else {
                    for (var key in GM.Form.formScope.result) {
                        if (key.indexOf('dg__') < 0) {
                            if (_this._datas.globe[key] && !$.isEmptyObject(_this._datas.globe[key]) &&
                                $.isEmptyObject(GM.Form.formScope.result[key])) {
                                GM.Form.formScope.result[key] = _this._datas.globe[key];
                            }
                        } else if (key.indexOf('dg__') === 0) {
                            if (_this._datas.globe[key].data.page.records.length > 0 &&
                                GM.Form.formScope.result[key].data.page.records.length === 0) {
                                GM.Form.formScope.result[key] = _this._datas.globe[key];
                            }
                        } else if (key.indexOf('page__') === 0) {
                            if (_this._datas.globe[key].totalItems > 0 &&
                                GM.Form.formScope.result[key].totalItems === 0) {
                                GM.Form.formScope.result[key] = _this._datas.globe[key];
                            }
                        }
                    }
                }
            } else {
                for (var key in _this._datas.globe) {
                    if (key == 'treeKey' || _this._datas.globe.treeKey[key]) {
                        continue;
                    }

                    GM.Form.formScope.result[key] = _this._datas.globe[key];
                }
            }
            //
            _this._bindDataGrid(GM.Form.formScope, null, _this._datas.globe);

            //
            if (GM.CommonOper.isStrNullOrEmpty(GM.Form.formScope.result.sc_spxx.SHRYXM)) {
                GM.Form.formScope.result.sc_spxx.SHRYXM = GM.userInfo.realName;
            }
            if (GM.CommonOper.isStrNullOrEmpty(GM.Form.formScope.result.sc_spxx.SHJSSJ)) {
                var pCurDate = new Date();
                var strFormatDate = GM.CommonOper.getFormatData(pCurDate.getTime());
                GM.Form.formScope.result.sc_spxx.SHJSSJ = strFormatDate;
            }
        }, function(res) {
            GM.Form.CommonOper.showFailureMsg('' + res.message);
        });
    },
    _getSavingData: function() {
        var data;

        if (this._datas.globe.treeKey) {
            //this.updateGlobalData();
            data = angular.copy(this._datas.globe);
        } else {
            data = angular.copy(GM.Form.formScope.result);
        }

        data = JSON.stringify(data);
        return data;
    },
    _saveDialog: function() {
        if (!GM.Form.formScope.dialogParameter.formid) {
            return;
        }

        var _this = this;
        var paras = {};
        angular.copy(GM.Form.parameters.urlParams, paras);

        if (GM.Form.formScope.dialogParameter.params) {
            for (var key in GM.Form.formScope.dialogParameter.params) {
                paras[key] = GM.Form.formScope.dialogParameter.params[key];
            }
        }

        var param = {
            formids: GM.Form.formScope.dialogParameter.formid,
            urlParameters: JSON.stringify(paras),
            keys: JSON.stringify(_this._datas.saveKeys),
            data: _this._getSavingData()
        };

        GM.Form.CommonOper.syncAjaxPost(GM.Form.parameters.servers.saveDataServer, function(res) {
            if (res.success === true) {
                GM.Form.CommonOper.showSuccessMsg('');
            } else {
                GM.Form.CommonOper.showFailureMsg(res.message);
            }
        }, function(res) {
            GM.Form.CommonOper.showFailureMsg(res.message);
        }, param);
    },
    /**
     * 
     *
     * @param {Boolean} autoHandOver        
     * @param {Function} successCallback    
     * @param {Function} failureCallback    
     */
    _saveData: function(autoHandOver, successCallback, failureCallback) {
        var _this = this;

        var param = {
            urlParameters: JSON.stringify(GM.Form.parameters.urlParams),
            keys: JSON.stringify(_this._datas.saveKeys),
            data: _this._getSavingData()
        };

        if (GM.Form.parameters.savingFormIds && GM.Form.parameters.savingFormIds.length > 0) {
            param.formids = GM.Form.parameters.savingFormIds.join(',');
        } else {
            param.formids = GM.Form.parameters.formIds.join(',');
        }

        GM.Form.CommonOper.syncAjaxPost(GM.Form.parameters.servers.saveDataServer, function(res) {
            if (res.success === true) {
                if (successCallback) {
                    successCallback();
                    return;
                }

                if (autoHandOver === true) {
                    GM.Form.formScope.handOver();
                } else {
                    GM.Form.CommonOper.showSuccessMsg('');
                }
            } else {
                if (failureCallback) {
                    failureCallback();
                }

                GM.Form.CommonOper.showFailureMsg(res.message);
            }
        }, function(res) {
            if (failureCallback) {
                failureCallback();
            }

            GM.Form.CommonOper.showFailureMsg(res.message);
        }, param);
    }
};


