import 'formCommonApi';
import 'agencyService';

GM.Form.Workflow = {
    init: function () {
        var _this = this;

        GM.Form.formScope.taskInfo = {
            bizKey: GM.Form.parameters.urlParams.ywh,
            draftBizKey: GM.Form.parameters.urlParams.did || GM.Form.parameters.urlParams.duuid, //
            processDefId: GM.Form.parameters.urlParams.processDefId,
            processKey: GM.Form.parameters.urlParams.processKey, //
            processName: GM.Form.parameters.urlParams.processName,
            taskKey: GM.Form.parameters.urlParams.taskKey, //
            isClaimed: false, //
            bizName: GM.Form.parameters.urlParams.caseTitle, //
            startTime: undefined, //
            forbid: false, //
            boxName: (GM.Form.parameters.urlParams.box || 'agency')
        }; //

        GM.Form.formScope.handOver = function () {
            Dialog = GM.Form.ngDialog.open({
                template: STATIC_RESOURCES_PATH + 'core-views/handOver.html',
                scope: GM.Form.formScope
            });
        };

        GM.Form.formScope.startProcessAndHandOver = function () {
            GM.Form.formScope.startProcess(true, true);
        };

        GM.Form.formScope.startProcess = function (autoHandOver, notTurnToFirst) {
            var ctrlCaseTitle = $("[data-role='bizname']");

            if (!ctrlCaseTitle || ctrlCaseTitle.length === 0) {
                GM.Form.CommonOper.showFailureMsg('');
                return;
            }

            ctrlCaseTitle = ctrlCaseTitle.val();

            if (!ctrlCaseTitle || ctrlCaseTitle == '') {
                GM.Form.CommonOper.showFailureMsg('');
                return;
            }

            var param = {
                appId: GM.Form.parameters.appId,
                userId: GM.Form.parameters.userInfo.loginName,
                processDefId: GM.Form.parameters.urlParams.processDefId,
                title: ctrlCaseTitle
            };

            if (GM.Form.formScope.taskInfo.draftBizKey) {
                param.draftKey = GM.Form.formScope.taskInfo.draftBizKey;
            }

            //
            var ctrlExtraBizDatas = angular.element("[data-role='biz-data']");
            var extraBizData;

            if (ctrlExtraBizDatas.length > 0) {
                for (var y = 0; y < ctrlExtraBizDatas.length; y++) {
                    var sModel = $(ctrlExtraBizDatas[y].attr('ng-model'));

                    if (sModel) {
                        var arr = sModel.split('.');

                        if (arr.length > 0) {
                            if (!extraBizData) {
                                extraBizData = {};
                            }

                            extraBizData[arr[arr.length - 1]] = eval('GM.Form.formScope.' + sModel);
                        }
                    }
                }
            }

            if (extraBizData) {
                param.extraBizData = extraBizData;
            }

            _this._startProcess(param).then(function (res) {
                if (res.success === true) {
                    //
                    GM.Form.parameters.urlParams.ywh = res.data.bizKey;
                    GM.Form.formScope.taskInfo.taskKey = res.data.currentTaskId;
                    GM.Form.formScope.taskInfo.bizKey = res.data.bizKey;
                    GM.Form.formScope.taskInfo.bizName = res.data.caseTitle;
                    GM.Form.formScope.taskInfo.startTime = res.data.startTime;
                    GM.Form.formScope.taskInfo.isClaimed = true;

                    if (autoHandOver === true) {
                        GM.Form.formScope.saveAndHandOver();
                    } else {
                        GM.Form.formScope.save();
                    }

                    _this.saveBizFlowInfo('', res.data.bizKey);
                    if (!notTurnToFirst) {
                        let str = '';
                        for (var key in GM.Form.parameters.urlParams) {
                            str += '&' + key + '=' + GM.Form.parameters.urlParams[key];
                        }
                        window.location = str.replace(/&/, '?') + '&taskKey=' + GM.Form.formScope.taskInfo.taskKey;
                    }
                }
            }, function (res) {});
        };

        //
        GM.Form.formScope.claimOrUnclaim = function (status) {
            var alertMessage, myUrl, erroeMessage;
            if (status === 'claim') {
                alertMessage = '';
                myUrl = FORM_REST_URL.workflow.claimTask;
                erroeMessage = '';
            } else {
                alertMessage = '';
                myUrl = FORM_REST_URL.workflow.unclaimTask;
                erroeMessage = '';
            }
            GM.Alert.show(alertMessage, '', null, function () {
                GM.Form.restful.post(myUrl, null, {
                    bizKey: GM.Form.formScope.taskInfo.bizKey,
                    taskKey: GM.Form.formScope.taskInfo.taskKey,
                    userId: GM.Form.parameters.userInfo.id
                }).then(function (res) {
                    if (res.success) {
                        let str = '';
                        for (var key in GM.Form.parameters.urlParams) {
                            str += '&' + key + '=' + GM.Form.parameters.urlParams[key];
                        }
                        window.location = str.replace(/&/, '?') + '&taskKey=' + GM.Form.formScope.taskInfo.taskKey;
                    } else {
                        GM.Infomation.show(erroeMessage, {
                            type: 'warning'
                        });
                    }
                }, function (error) {
                    GM.Infomation.show(error, {
                        type: 'warning'
                    });
                });
            });
        };
        //
        GM.Form.formScope.showWfGraph = function () {
            GM.Form.formScope.blankTitle = '';
            GM.Form.formScope.htmlContent = '<img src="' + FORM_REST_URL.workflow.getHighLightTask + '?bizKey=' + GM.Form.formScope.taskInfo.bizKey + '" style="margin-top:30px;"/>';
            //GM.Form.formScope.htmlContent = '<img src="http://192.168.5.27:8080/activiti-web/usertask/getHighLightTask?bizKey=Z201803230005"/>';
            Dialog = GM.Form.ngDialog.open({
                template: STATIC_RESOURCES_PATH + 'core-views/model/blank.html',
                scope: GM.Form.formScope
            });
        };

        this._isTaskClaimed();
        this._initBizFlowDetails();
    },
    _initBizFlowDetails: function () {
        var _this = this;
        //
        GM.Form.formScope.pendingPageLz = {
            currentPage: 1
        };

        GM.Form.formScope.$watch('pendingPageLz.currentPage', function () {
            if (GM.Form.formScope.pendingPageLz.currentPage > 0) {
                _this._getLzData();
            }
        });

        //
        GM.Form.formScope.lzGridOptions = {};

        //
        var strCellTemplate = '<span style="display:block;padding:5px;color:#3280fc"></span>';

        GM.Form.formScope.lzGridOptions.columnDefs = [{
            name: 'taskKey',
            displayName: '',
            enableColumnMenu: false,
            cellTooltip: true
        },
        {
            name: 'assigneeName',
            displayName: '',
            enableColumnMenu: false,
            cellTooltip: true
        },
        {
            name: 'previousTaskKey',
            displayName: '',
            enableColumnMenu: false,
            cellTooltip: true
        },
        {
            name: 'submitterName',
            displayName: '',
            enableColumnMenu: false,
            cellTooltip: true
        },
        {
            name: 'startTime',
            displayName: '',
            enableColumnMenu: false,
            cellTooltip: true
        },
        {
            name: 'completeTime',
            displayName: '',
            enableColumnMenu: false,
            cellTooltip: true
        },
        {
            name: 'yj',
            displayName: '',
            enableColumnMenu: false,
            cellTemplate: strCellTemplate
        }
        ];

        //
        GM.Form.formScope.showLzDialog = function () {
            //
            Dialog = GM.Form.ngDialog.open({
                template: STATIC_RESOURCES_PATH + 'core-views/model/liuzhuan-modal.html',
                scope: GM.Form.formScope,
                className: 'ngdialog-theme-default custom' //
            });

            //
            _this._getLzData();
        };

        //
        GM.Form.formScope.viewMap = function () {
            var strDyh = GM.Form.formScope.result.zrzyjbxx.ZRZYDJDYH;
            var strUrl = GM.LayoutOper.addURLParam(ONEMAP_PATH, 'ProjectType', 'zrzy');
            strUrl = GM.LayoutOper.addURLParam(strUrl, 'ZRZYDJDYH', strDyh);
            GM.FullModal.show('', strUrl);
        };

        //
        GM.Form.formScope.closeDialog = function () {
            Dialog.close();
        };
    },
    _getLzData: function () {
        var url = window.FORM_REST_URL.workflow.getTaskTrackPage + '?current=' + GM.Form.formScope.pendingPageLz.currentPage + '&orderField=START_TIME&bizKey=' + GM.Form.formScope.taskInfo.bizKey;
        GM.Form.restful.get(url, function (response) {
            if (response.success === true) {
                var dbData = response.data.records;

                angular.forEach(dbData, function (item, index) {
                    item.index = index + 1;
                    item.startTime = GM.Form.filter('date')(item.startTime, 'yyyy-MM-dd HH:mm:ss');
                    item.completeTime = GM.Form.filter('date')(item.completeTime, 'yyyy-MM-dd HH:mm:ss');
                });

                GM.Form.formScope.pendingPageLz.totalItems = response.data.total;
                GM.Form.formScope.lzGridOptions.data = response.data.records;
            }
        }, function (res) {});
    },
    _isTaskClaimed: function () {
        var url = FORM_REST_URL.workflow.isClaimed + '?bizKey=' + GM.Form.formScope.taskInfo.bizKey + '&taskkey=' + encodeURIComponent(GM.Form.formScope.taskInfo.name);
        GM.Form.restful.get(url, null).then(function (response) {
            GM.Form.formScope.taskInfo.isClaimed = response.success === true;
        }, function (response) {});
    },
    _startProcess: function (param) {
        return GM.Form.restful.get(FORM_REST_URL.workflow.startProcess, param);
    },
    saveBizFlowInfo: function (id, bizKey) {
        GM.Form.restful.get(GM.Form.parameters.servers.updateBizFlowInfoServer + '?bizKey=' + bizKey + '&id=' + id).then(function (res) {}, function (res) {});
    }
};

window.workflowCore = {

};



